import React from "react"
import GitHubIcon from "@material-ui/icons/GitHub"
import IconButton from "@material-ui/core/IconButton"
import LinkedInIcon from "@material-ui/icons/LinkedIn"
import { makeStyles } from "@material-ui/core/styles"
import "../site.css"
import { githubLink, linkedinLink } from "../pages/index"

const footerStyles = makeStyles({
  root: {
    display: "flex",
    flexDirection: "column",
    fontFamily: `Sen, sans-serif`,
    alignItems: "center",
    background: "#121212",
    padding: "20px 10px",
  },
  buttons: { display: "flex", justifyContent: "space-evenly" },
})

const Footer = () => {
  const classes = footerStyles()
  return (
    <div className={classes.root}>
      <div style={{ color: "#fff", fontSize: "1.5em" }}>
        andrewstolzle@gmail.com
      </div>
      <div>
        <IconButton
          style={{ color: `#fff` }}
          onClick={() => window.open(githubLink, "noopener")}
        >
          <GitHubIcon size="large" />
        </IconButton>
        <IconButton
          style={{ color: `#fff` }}
          onClick={() => window.open(linkedinLink, "noopener")}
        >
          <LinkedInIcon size="large" />
        </IconButton>
      </div>
      <div style={{ color: "#fff", fontSize: "1em" }}>
        ¬© {new Date().getFullYear()}, Andrew Stolzle
      </div>
    </div>
  )
}

export default Footer
